.scroll-to-top {
  position: fixed;
  bottom: 40px;
  right: 30px;
  padding: 12px 16px;
  font-size: 20px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 999;
  transition: all 0.3s ease;
}

.scroll-to-top:hover {
  background-color: #444;
}
